import axios from 'axios';
// example)
//   onclick="easyDownload.run('https://example.com/path/to/filename.pdf', 'pdf', 'filename')"
export var run = function (requestUrl, fileExtension, filename) {
    axios({
        url: requestUrl,
        method: 'get',
        responseType: 'arraybuffer'
    })
        .then(function (response) {
        var blob = new Blob([response.data], {
            type: "application/" + fileExtension
        });
        var URL = window.URL || window.webkitURL;
        var downloadUrl = URL.createObjectURL(blob);
        var link = document.createElement('a');
        if (window.navigator.msSaveBlob) {
            // IE11
            window.navigator.msSaveBlob(blob, filename + "." + fileExtension);
        }
        else if (typeof link.download === 'undefined') {
            // iPhone
            window.location.href = downloadUrl;
        }
        else {
            link.href = downloadUrl;
            link.download = filename + "." + fileExtension;
            document.body.appendChild(link);
            link.click();
        }
    })
        .catch(function (error) {
        console.error("http status=" + error.response.status + ", body=" + error.response.data);
    });
};
